@import 'element';
//template
.templateTitle{
	padding: 50px 0 0 0;
	.title-text{
		font-family: $fontArchivo;
		font-weight: 400;
	}
}
.templateContent{
	padding: 0 0 50px 0 ;
	font-family: $fontLora;
	color: $color2;
	font-size: 14px;
	line-height: 1.7;
	font-weight: 400;
	overflow: auto;
	h1{
		line-height: 1.2;
		color: $color2;
		font-size: 35px;
		font-weight: 500;
		margin: 0 0 10px 0;
	}
	h2{
		line-height: 1.2;
		color: $color2;
		font-size: 30px;
		font-weight: 500;
		margin: 0 0 10px 0;
	}
	h3{
		color: $color2;
		font-size: 25px;
		line-height: 1.2;
		font-weight: 500;
		margin: 0 0 10px 0;
	}
	h4{
		color: $color2;
		font-size: 20px;
		line-height: 1.2;
		font-weight: 500;
		margin: 0 0 10px 0;
	}
	h5{
		line-height: 1.2;
		color: $color2;
		font-size: 18px;
		font-weight: 500;
		margin: 0 0 10px 0;
	}
	h6{
		color: $color2;
		font-size: 16px;
		line-height: 1.2;
		font-weight: 500;
		margin: 0 0 10px 0;
	}
	ul{
		margin: 0 0 20px 0;
		li{
			position: relative;
			padding: 0 0 0 10px;
			margin: 0 0 10px 0;
			line-height: 1.2;
			&:before{
				content: '-';
				position: absolute;
				top: 0;
				left: 0;
			}
		}
	}
	ol{
		counter-reset:myCounter;
		margin: 0 0 20px 0;
		li{
			position: relative;
			padding: 0 0 0 30px;
			margin: 0 0 10px 0;
			line-height: 1.2;
			&:before{
				counter-increment: myCounter;
				content:counter(myCounter)'. ';
				position: absolute;
				top: 0;
				left: 0;

			}
		}
	}
	table{
		overflow: auto;
		border-collapse: collapse;
		width: 100%;
		padding: 0 0 10px 0;
		display: block;
		tr{
			border: 1px solid $color5;
		}
		td{
			line-height: 1.2;
			color: $color2;
			font-weight: 400;
			padding: 5px;
			text-align: left;
			border: 1px solid $color5;
		}
		th{
			line-height: 1.2;
			color: $color5;
			font-weight: 600;
			padding: 5px;
			text-align: left;
			border: 1px solid $color5;
		}
	}
}
@media (min-width: 768px){
	.templateTitle{
		padding: 65px 0 0 0;
	}
	.templateContent{
		padding:0px 0 65px 0;
	}
}
@media (min-width: 1024px){
	.templateTitle{
		padding: 90px 0 0px 0;
		.title-text{
			font-family: $fontArchivo;
			font-weight: 400;
		}
	}
	.templateContent{
		padding: 0px 0 90px 0;
		font-size: 16px;
		h3{
			font-family: $fontLora;
			color: $color2;
			font-size: 28px;
			font-weight: 500;
		}
	}
}
@media (min-width: 1280px){
	.templateContent{
		.container-right{
			width: 56%;
		}
	}
	.templateContent{
		font-size: 18px;
		h1{
			font-size: 45px;
		}
		h2{
			font-size: 40px;
		}
		h3{
			font-size: 35px;
		}
		h4{
			font-size: 30px;
		}
		h5{
			font-size: 28px;
		}
		h6{
			font-size: 26px;
		}
	}
}
@media (min-width: 1440px){
	.templateTitle{
		padding: 125px 0 0px 0;
	}
	.templateContent{
		padding: 0 0 125px 0 ;
	}
}
@media (min-width: 1920px){
	.templateTitle{
		padding: 265px 0 30px 0;
		.title-section{
			margin: 0 0 4px 0;
		}
		.title-text{
			font-family: $fontArchivo;
			font-weight: 400;
		}
	}
	.templateContent{
		padding: 48px 0 296px 0;
		font-family: $fontLora;
		color: $color2;
		font-size: 18px;
		font-weight: 400;
		h3{
			font-family: $fontLora;
			color: $color2;
			font-size: 28px;
			font-weight: 500;
		}
	}
}
