@import 'element';
.pageContact{
	.contact{
		padding: 50px 0;
	}
	.contact__img {
		top: 0px;
	}
}
.map{
	height: 423px;
	iframe{
		width: 100%;
		height: 100%;
		filter: grayscale(100%);
	}
}
@media (min-width: 480px){
	.pageContact{
		.contact__img {
			width: 240px;
		}
	}
}
@media (min-width: 768px){
.pageContact{
	.contact{
		padding: 65px 0;
	}
}
}
@media (min-width: 1280px){
	.pageContact{
		.contact{
			padding: 90px 0;
		}
	}
}
@media (min-width: 1440px){
	.pageContact{
		.contact{
			padding: 125px 0;
		}
	}
}

@media (min-width: 1920px){
	.pageContact{
		.contact {
			padding:263px 0 178px 0;
			.title-section {
				margin: 0 0 82px 0;
			}
			.container {
				padding: 0 17px 0 108px;
			}
		}
		.contact__list {
			margin: 0 0 36px 0;
		}
		.contact__img {
			top: 44px;
			width: 676px;
		}
	}
}
