@import 'element';
.not-found-page{
	background-image: url("../img/errorBg.jpg");
	width: 100%;
	background-size: cover;
	background-repeat: no-repeat;
	.title{
		color: $color1;
		margin: 0 0 20px 0;
	}
	.btn{
		margin: 0 auto;
	}
}
.not-found-page__header{
	padding: 25px 0 25px 0;
}
.not-found-page__logo {
	width: 130px;
	height: auto;
	display: block;
	margin: 0 auto;
	img{
		width: 100%;
		object-fit: cover;
	}
}
.not-found-page__text{
	font-family: $fontLora;
	color:$color2;
	padding: 0;
	font-size: 125px;
	font-weight: 400;
	line-height: 1;
	margin: 0 0 20px 0;
}
.not-found-page__content{
	padding: 50px 0;
	text-align: center;
}
@media (min-width: 768px){
	.not-found-page__content{
		padding: 65px 0;
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap;
		text-align: initial;
		.title{
			order: 1;
			width: 46%;
			align-self: center;
			box-sizing: border-box;
		}
		.btn{
			order: 3;
			margin: 0;
		}
	}
	.not-found-page__text{
		order: 2;
		width: 54%;
		font-size: 175px;
		box-sizing: border-box;
		margin: 0;
	}
	.not-found-page__logo{
		margin: 0;
	}
}
@media (min-width: 1024px){
	.not-found-page__content{
		.title {
			font-size: 56px;
		}
	}
	.not-found-page__text {
		font-size: 235px;
	}
}
@media (min-width: 1280px){
	.not-found-page__header{
		padding: 30px 0;
	}
	.not-found-page__content{
		.title {
			font-size: 70px;
		}
	}
	.not-found-page__text {
		font-size:310px;
	}
}
@media (min-width: 1440px){
	.not-found-page__content{
		padding: 70px 0;
	}
	.not-found-page__content{
		.title {
			font-size: 75px;
		}
	}
	.not-found-page__text {
		font-size: 335px;
	}
}
@media (min-width: 1600px){
	.not-found-page__content{
		.title {
			font-size: 84px;
		}
	}
	.not-found-page__text {
		font-size: 370px;
	}
}
@media (min-width: 1920px){
	.not-found-page{
		.container{
			padding: 0px 17px 0 106px;
		}
		.btn{
			padding: 18px 46px 18px 46px;
		}
	}
	.not-found-page__header{
		padding: 56px 0;
	}
	.not-found-page__logo{
		width: 169px;
	}
	.not-found-page__content{
		.title{
			font-size: 94px;
			padding: 40px 0 0 0;
		}
	}
	.not-found-page__text{
		font-size: 450px;
	}
}
