@import 'element';
.footer{
	background-color: #f1f1f1;
	padding: 53px 0 0 0;
}
.footer__main{
	display: flex;
	justify-content: flex-start;
	flex-wrap: wrap;
	padding: 0 0 15px 0;
}
.footer__logo{
	width: 130px;
	height: auto;
	display: block;
	margin: 0 auto 20px;
	img{
		width: 100%;
		object-fit: cover;
	}
}
.menu-footer__list{
	display: flex;
	align-self: center;
	justify-content: center;
	flex-wrap: wrap;
	text-align: center;
	box-sizing: border-box;
	width: 100%;
	li{
		color:$color2;
		font-size: 14px;
		font-weight: 300;
		font-family: 'Roboto';
		width: 100%;
		margin: 0 0 10px 0;
		&:last-child{
			margin: 0;
		}
	}
}
.menu-footer__link{
	color: $color2;
	font-size: 14px;
	font-weight: 300;
	font-family: 'Roboto';
	transition: 0.5s;
	display: inline-block;
	&:hover{
		color: $color5;
	}
	strong{
		color: $color5;
		font-size: 14px;
		font-weight: 500;
		font-family: 'Roboto';
		margin: 0 0 0 5px;
		transition: 0.5s;
		&:hover{
			color: $color4;
		}
	}
}
.footer__bottom{
	border-top: 1px solid #dfdfdf;
	display: flex;
	justify-content: space-between;
	padding: 10px 0;
}
.bottom__menu{
	display: flex;
	justify-content: flex-end;
	li{
		margin: 0 0 0 10px;
	}
}
.bottom__menu-linc{
	color: $color2;
	font-size: 13px;
	font-weight: 300;
	font-family: 'Roboto';
	transition: 0.5s;
	&:hover{
		color: $color4;
	}
}
.bottom__text{
	color: $color2;
	font-size: 13px;
	font-weight: 300;
	font-family: 'Roboto';
	align-self: center;
}
@media (min-width: 768px){
	.footer{
		padding: 54px 0 0 0;
	}
	.footer__logo{
		width: 166px;
		height: 58px;
	}
}
@media (min-width: 1024px){
	.footer__logo {
		margin: 0 30px 0 0;
	}
	.menu-footer__list{
		width: 75%;
		justify-content: space-between;
		text-align: initial;
		li{
			margin: 0 3\ % 0 0;
			width: auto;
			&:last-child{
				margin: 0;
			}
		}
	}
	.bottom__text {
		justify-content: flex-end;
	}
	.bottom__menu{
		li{
			margin: 0 0 0 10px;
		}
	}
}
@media (min-width: 1280px){
	.footer__main{
		padding: 0 0 50px 0;
	}
	.footer__body {
		padding: 0 0 0px 0;
	}
}
@media (min-width: 1920px){
	.footer{
		.container{
			padding: 0 113px 0 113px;
		}
	}
	.footer__main{
		padding: 0 0 70px 0;
	}
	.footer__logo{
		width: 180px;
		height: 63px;
		margin: 0 127px 0 0;
	}
	.menu-footer__list{
		width: calc(100% - 307px);
		padding:0;
		li{
			font-size: 14px;
		}
	}
	.menu-footer__link{
		font-size: 14px;
		font-weight: 300;
		strong{
			font-size: 14px;
		}
	}
	.footer__bottom{
		padding: 7px 0 10px 0;
	}
	.bottom__menu{
		li{
			margin: 0 0 0 20px;
		}
	}
	.bottom__menu-linc{
		font-size: 13px;
	}
	.bottom__text{
		font-size: 13px;
	}
}
