@import 'element';
//leistungenContent
.leistungenContent{
	padding: 50px 0 ;
	position: relative;
}
.column{
	color: $color2;
	font-family: $fontLora;
	font-weight: 400;
	font-size: 16px;
	line-height: 1.9;
}
//leistungenBanner
.leistungenBanner__img{
	img{
		display: block;
		width: 100%;
		height: 100%;
		object-fit: cover;
	}
}
.leistungenBanner__col{
	background-color: #f3f4f0;
	min-height: 140px;
	position: relative;
	.workSchedule__title {
		margin: 0 0 23px 0;
	}
	.img{
		position: absolute;
		right: -46px;
		top: 75px;
		width: 140px;
		height: auto;
	}
	img{
		display: block;
		width: 100%;
		height: 100%;
		object-fit: cover;
	}
	width: 100%;
}
.leistungenBanner__table{
	overflow: auto;
	table{
		min-width: 510px;
		display: block;
	}
	td{
		color: #a3a3a3;
		font-family: $fontArchivo;
		font-weight: 400;
		font-size: 16px;
		line-height: 1.5;
		padding: 6px 10px;
		&:first-child {
			padding: 6px 6px 6px 0;
			color: #a3a3a3;
		}
	}
}
.row{
	display: flex;
	justify-content: space-between;
	width: 100%;
	min-height: 140px;
	picture{
		width: 100%;
		height: 100%;
		object-fit: cover;
	}
}
.right-block{
	.leistungenBanner__text{
		padding: 30px 15px 30px 15px;
	}
}
.left-block{
	.leistungenBanner__text{
		padding: 30px 15px 80px 15px;
	}
}
.leistungenBanner__text{
	blockquote{
		color: $color4;
		font-size: 20px;
		line-height: 1.5;
		margin: 0 0 10px 0;
		font-family: $fontLora;
		font-weight: 400;
	}
	span{
		color: $color5;
		font-size: 14px;
		line-height: 1.2;
		font-weight: 400;
		font-style: italic;
		margin: 0 0 10px 0;
		display: block;
		font-family: $fontArchivo;
	}
}
.bgGreen{
	background-color: $color4;
}
.leistungenBanner__blockLogo{
	position: absolute;
	top: -62px;
	left: 40px;
	width: 140px;
	height: auto;
	img{
		width: 100%;
		height: 100%;
		object-fit: cover;
	}
}
.imgTop{
	position: absolute;
	right: 0px;
	top: -62px;
	width: 162px;
	z-index: 1;
	height: auto;
}
//leistungenAbout
.leistungenAbout{
	padding: 50px 0;
	font-family: $fontLora;
	font-weight: 400;
	font-size: 16px;
	line-height: 1.7;
	color:$color2;
	.title-section{
		margin: 0 0 35px 0px;
	}
	strong{
		font-family: $fontLora;
		font-weight: 700;
	}
	a{
		color: $color4;
		font-family: $fontLora;
		font-weight: 700;
		transition: 0.5s;
		&:hover{
			color: $color5;
		}
	}
	ul{
		margin: 0 0 20px 0;
		padding: 0 10px 0 0;
	}
	li{
		color: $color5;
		font-family: $fontLora;
		font-weight: 400;
		font-size: 16px;
		line-height: 1.5;
		position: relative;
		padding: 0 0 0 10px;
		margin: 0 0 10px 0;
		&:before{
			content: '-';
			color: $color5;
			position: absolute;
			top: 0;
			left: 0;
			font-size: 18px;
		}
	}
}
.leistungenAbout__text{
	display: flex;
	justify-content: space-between;
	flex-wrap: wrap;
}
@media (min-width: 768px){
	.leistungenContent{
		padding: 65px 0;
	}
	.leistungenAbout{
		padding: 65px 0;
	}
	.leistungenBanner__col {
		width: 50%;
		display: flex;
	}
	.right-block{
		justify-content: flex-start;
	}
	.left-block{
		justify-content: flex-end;
	}
	.leistungenBanner__text{
		width: 324px;
		box-sizing: border-box;
	}
}
@media (min-width: 1024px){
	.leistungenBanner__text{
		width: 432px;
	}
}
@media (min-width: 1280px){
	//leistungenContent
	.leistungenContent{
		padding: 90px 0 90px 0;
		position: relative;
		.title-section{
			margin: 0 0 79px 0;
		}
	}
	.column{
		columns: 2;
		column-gap: 57px;
		font-weight: 400;
		font-size: 18px;
	}
	//leistungenBanner
	.leistungenBanner__col{
		.workSchedule__title {
			margin: 0 0 23px 0;
		}
		.img{
			right: -122px;
			top: 78px;
			width: 330px;
		}
		img{
			display: block;
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
		width: 50%;
		td{
			padding: 6px 10px;
			&:first-child {
				padding: 6px 6px 6px 0;
			}
		}
	}
	.right-block{
		.leistungenBanner__text{
			padding: 50px 30px;
		}
	}
	.left-block{
		.leistungenBanner__text{
			padding: 50px 15px;
		}
	}
	.leistungenBanner__text{
		width: 540px;
		blockquote{
			font-size: 34px;
			margin: 0 0 10px 0;
		}
		span{
			font-size: 16px;
			margin: 0 0 29px 0;
		}
	}
	.leistungenBanner__blockLogo{
		top: -102px;
		left: 160px;
		width: 230px;
	}
	.imgTop{
		top: -132px;
		width: 300px;
	}
	//leistungenAbout
	.leistungenAbout{
		padding: 90px 0 90px 0;
		font-size: 18px;
		.title-section{
			margin: 0 0 35px 0px;
		}
		ul{
			width: 47%;
			margin: 0 0 63px 0;
		}
		li{
			font-weight: 400;
			font-size: 18px;
			padding: 0 0 0 10px;
			margin: 0 0 23px 0;
		}
	}
	.leistungenAbout__text{
		width:63%;
	}
}
@media (min-width: 1440px){
	.leistungenContent{
		padding: 125px 0;
	}
	.leistungenAbout{
		padding: 125px 0;
	}
	.left-block{
		.leistungenBanner__text{
			padding: 50px 55px 50px 0px;
		}
	}
	.right-block{
		.leistungenBanner__text{
			padding: 50px 0 50px 55px;
		}
	}
	.leistungenBanner__text{
		width: 580px;
	}
}
@media (min-width: 1600px){
	.leistungenBanner__text{
		width: 640px;
	}
	.left-block{
		.leistungenBanner__text{
			padding: 50px 112px 50px 0px;
		}
	}
	.right-block{
		.leistungenBanner__text{
			padding: 50px 0 50px 112px;
		}
	}
	.imgTop{
		top: -180px;
		width: 400px;
	}
}
@media (min-width: 1920px){
	//leistungenContent
	.leistungenContent{
		padding:265px 0 210px 0;
	}
	.leistungenContent {
		.title-section {
			margin: 0 0 109px 0;
		}
	}
	.column{
		width: 77%;
	}
	//leistungenBanner
	.leistungenBanner__col{
		.img{
			right: -261px;
			top: 165px;
			width: 490px;
		}
		td{
			font-size: 18px;
		}
	}
	.leistungenBanner__text{
		width: 905px;
	}
	.leistungenBanner__blockLogo{
		top: -118px;
		left: 160px;
		width: 498px;
	}
	.imgTop{
		top: -243px;
		width: 482px;
	}
	//leistungenAbout
	.right-block {
		.leistungenBanner__text {
			padding: 123px 0 50px 129px;
		}
	}
	.left-block {
		.leistungenBanner__text {
			padding: 73px 80px 90px 111px;
		}
	}
	.leistungenAbout {
		padding: 192px 0 210px 0;
		.title-section {
			margin: 0 0 64px 0px;
		}
	}
	.leistungenAbout__text{
		width:63%;
	}
}
