$main-ff: "Roboto";

@mixin font($fname, $fstyle, $fweight, $furl) {
  @font-face {
    src: url($furl) format("woff");
    font-family: $fname;
    font-weight: $fweight;
    font-style: $fstyle;
  }
}

@include font("Roboto", normal, 400, "../fonts/Roboto-Regular.ttf");
@include font("Roboto", normal, 600, "../fonts/Roboto-Bold.ttf");
@include font("Roboto", normal, 200, "../fonts/Roboto-Light.ttf");
@include font("Roboto", normal, 500, "../fonts/Roboto-Medium.ttf");
@include font("Archivo", normal, 400, "../fonts/Archivo-Regular.ttf");
@include font("Archivo", normal, 600, "../fonts/Archivo-SemiBold.ttf");
@include font("Archivo", normal, 700, "../fonts/Archivo-Bold.ttf");
@include font("Archivo", italic, 400, "../fonts/Archivo-Italic.ttf");

@include font("Lora", normal, 400, "../fonts/Lora-Regular.ttf");
@include font("Lora", normal, 500, "../fonts/Lora-Medium.ttf");
@include font("Lora", italic, 400, "../fonts/Lora-Italic.ttf");
@include font("Lora", normal, 600, "../fonts/Lora-SemiBold.ttf");
