@import 'element';
//banner
.main-screen{
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	width: 100%;
	position: relative;
	transition: 0.5s;
}
.banner{
	transition: 0.5s;
	box-sizing: border-box;
	position: relative;
	padding: 150px 0 0 0;
	min-height: 100vh;
	display: flex;
	.container{
		align-self: stretch;
		height: auto;
		padding: 0 15px 80px;
	}
}
.banner__text{
	padding: 16vh 0 10vh;
	position: relative;
	z-index: 1;
	.title{
		margin: 0 0 10px 0;
	}
	.title-text{
		color: $color2;
		font-family: $fontLora;
		font-weight: 400;
	}
}
.banner__img{
	position: absolute;
	z-index: -1;
	right: 15px;
	box-sizing: border-box;
	width: 80%;
	height: 100%;
	bottom: 0;
	max-width: 1440px;
	img{
		width: 100%;
		height: 100%;
		object-fit: cover;
		display: block;
		object-position: center;
	}
}

//company
.company{
	padding: 50px 0;
}
.company__text-strong{
	font-size: 20px;
	line-height: 1.6;
	color: $color2;
	font-family: $fontArchivo;
	font-weight: 400;
	margin: 0 0 10px 0;
	padding: 0 0px 0 0;
}
.company__text{
	font-size: 16px;
	line-height: 1.9;
	font-family: $fontArchivo;
	font-weight: 400;
	margin: 0 0 30px 0px;
	color: $color2;
}
//services
.services{
	background: $color2;
	padding: 50px 0;
	.title-section{
		color: $color1;
		margin: 0 0 20px 0px;
	}
}
.services__title-text{
	color: $color4;
	margin: 0 0 12px 0px;
	font-family: $fontArchivo;
	font-weight: 400;
	font-size: 16px;
}
.tabs{
	margin: 0px 0 20px 0;
}
.tabs__caption{
	width: 100%;
	margin: 0 0 10px 0;
	li{
		width: 100%;
		margin: 0 0 10px 0;
		overflow: hidden;
		&:last-child{
			margin: 0;
		}
	}

}
.tabs__link{
	color: #565656;
	word-wrap: break-word;
	font-size: 18px;
	cursor: pointer;
	transition: 0.5s;
	font-family: $fontArchivo;
	font-weight: 400;
	line-height: 1.2;
	display: inline;
	&:hover{
		color: $color4;
	}
	&.active{
		color: $color4;
		text-decoration: line-through;
	}
}
.tab-container{
	min-height: 350px;
	overflow: hidden;
	height: auto;
	display: block;
}
.tabs__content {
	display: none;
	transition: 0.5s;
	&.active{
		display: block;
		transition: 0.5s;
	}
}
.tab-container{
	margin: 0 auto;
	width: 100%;
}
.tabs__img{
	width: 100%;
	height: 100%;
	margin:0 auto 10px;
	max-width: 816px;
	transition: 0.5s;
	img{
		width: 100%;
		height: 100%;
		object-fit: cover;
	}
}
.tabs__img-text{
	font-size: 14px;
	transition: 0.5s;
	font-family: $fontLora;
	font-weight: 400;
	color: $color1;
	font-style: italic;
}
.services__link{
	font-size: 14px;
	color: $color1;
	font-family: $fontArchivo;
	font-weight: 600;
	text-transform: uppercase;
	transition: 0.5s;
	&:hover{
		color: $color4;
	}
}

//about
.about__banner{
	img{
		display: block;
		width: 100%;
	}
}
.about__content{
	position: relative;
	&:before{
		content: '';
		width: 100%;
		height: 145px;
		left: 0;
		top: -1px;
		background-color: $color4;
		position: absolute;
		z-index: -1;
	}
	&:after{
		content: '';
		height: 75%;
		right: 0;
		top: 145px;
		background-color: $color1;
		position: absolute;
		z-index: -1;
		width: 100%;
	}
}
.about__img{
	padding: 0 0 50px 0;
	width: 100%;
	position: relative;
	img{
		transform: translate(0px, -44px);
		width: 100px;
	}
}
.img-logo{
	position: absolute;
	right: 42px;
	bottom: -5px;
	transform: translate(0, 0);
	img{
		transform: translate(0, 0);
		width: 50px;
			animation: 7s linear 0s normal none infinite running rot;
		@keyframes rot {
			0% {
				transform: rotate(0deg);
			}
			100% {
				transform: rotate(360deg);
			}
		}
		@-webkit-keyframes rot {
			0% {
				transform: rotate(0deg);
			}
			100% {
				transform: rotate(360deg);
			}
		}
	}
}
.about__text{
	width: 100%;
	padding: 0px 0px 0 0px;
	box-sizing: border-box;
	color: $color3;
	font-size: 16px;
	font-family: $fontLora;
	font-weight: 400;
	blockquote{
		color: $color4;
		font-size: 20px;
		margin: 0 0 10px 0;
		font-family: $fontLora;
		font-weight: 400;
	}
	span{
		color: $color5;
		font-size: 14px;
		font-weight: 400;
		font-style: italic;
		margin: 0 0 10px 0;
		display: block;
		font-family: $fontLora;
		font-weight: 400;
	}
}


//contact
.contact{
	padding: 50px 0;
	.title-section{
		margin: 0 0 10px 0;
	}
}
.contact__list{
	width: 100%;
	margin: 0 0 10px 0;
	td{
		color: $color2;
		font-size: 16px;
		font-family: $fontLora;
		font-weight: 400;
		font-style: normal;
		align-self: center;
		transition: 0.5s;
		padding: 3px 10px 3px 0;
		&:first-child{
			color: $color4;
			font-family: $fontLora;
			font-weight: 600;
		}
		a{
			color: inherit;
			&:hover{
				color: $color4;
			}
		}
	}
}

.contact__img{
	position: absolute;
	right: 0;
	top:0;
	width: 150px;
	height: auto;
	z-index: -1;
}
.contact__workSchedule{
	display: flex;
	justify-content: flex-start;
	flex-wrap: wrap;
	margin: 30px 0 0 0;
}
.workSchedule__title{
	color: $color4;
	font-family: $fontLora;
	font-weight: 400;
	font-size: 20px;
	margin: 0 0 8px 0;
	width: 100%;
}
.workSchedule__list{
	display: flex;
	flex-wrap: wrap;
	flex-direction: column;
	width: 100%;
	box-sizing: border-box;
	padding: 0 10px 0 0;
}
.workSchedule__item{
	font-size: 16px;
	color: $color2;
	font-family: $fontLora;
	font-weight: 400;
	strong{
		font-weight: 600;
		font-family: $fontLora;
		font-size: 16px;
		color: $color2;
	}
}
