@import 'element';
//team
.team{
	padding: 50px 0;
}
.team__dr-card{
	display: flex;
	justify-content: flex-start;
	flex-wrap: wrap;
}
.team-group__card{
	display: flex;
	justify-content: flex-start;
	flex-wrap: wrap;
}
.dr-card__img{
	width: 133px;
	margin: 0 auto 20px;
	img{
		width: 100%;
		height: auto;
		object-fit: cover;
	}
}
.dr-card__info{
	display: flex;
	flex-wrap: wrap;
	justify-content: flex-start;
	width:100% ;
}
.dr-card__title{
	font-size: 18px;
	color: $color2;
	font-family: $fontLora;
	font-weight: 500;
	width: 100%;
	margin: 0 0 20px 0;
	text-align: center;
}
.dr-card__list{
	width: 100%;
}
.dr-card__item{
	font-family: $fontLora;
	font-weight: 400;
	font-size: 14px;
	color:$color2;
	margin: 0 0 5px 0;
	text-align: center;
	line-height: 1.6;
}
//team-group
.team-group{
	position: relative;
	//background-color: #5d6b45;
	.img{
		position: absolute;
		top: -20px;
		right: 0px;
		width: 70px;
		z-index: 1;
	}
}
.team-group__card{
	display: flex;
	justify-content: flex-start;
	flex-wrap: wrap;
	width: 100%;
	position: relative;
	z-index: 1;
	padding: 30px 0;
}
.leftBg{
	background-image: url("../img/aboutBg.png");
	background-repeat: no-repeat;
	background-size: cover;
	background-position: center;
}
.rightBg{
	background-color: $color5;
}
.team-group__img{
	width: 175px;
	position: relative;
	margin: 0 auto 20px;
	.imgCompany{
		position: absolute;
		top: 0;
		left: 0;
	}
	img{
		width: 100%;
		height: auto;
		object-fit: cover;
	}
	&.logoIcon{
		&:before{
			content: '';
			background-image: url(.././img/logoG.png);
			width: 156px;
			height: 130px;
			background-size: contain;
			position: absolute;
			left: -82px;
			top: -2px;
			background-repeat: no-repeat;
		}
	}
}
.team-group__info{
	width: 100%;
	text-align: center;
}
.team-group__name{
	color: $color1;
	font-size: 18px;
	font-family: $fontLora;
	font-weight: 400;
	line-height: 1.6;
	margin: 0 0 10px 0;
	word-wrap: break-word;
}
.team-group__item{
	color: #f1f1f1;
	font-size: 14px;
	font-family: 'Archivo';
	font-weight: 400;
	margin: 0 0 5px 0;
	line-height: 1.6;
	word-wrap: break-word;
}
@media (min-width: 768px){
	.team{
		padding: 65px 0;
	}
	.team-group{
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap;
		.img {
			top: -30px;
			width: 130px;
		}
	}
	.rightBg{
		width: 50%;
		display: flex;
		justify-content: flex-start;
		.team-group__card{
			justify-content: flex-start;
		}
	}
	.leftBg{
		width: 50%;
		display: flex;
		justify-content: flex-end;
		.team-group__card{
			justify-content: flex-end;
		}
	}
	.team-group__card{
		max-width: 324px;
		align-self: flex-start;
		padding: 50px 0;
	}
	.dr-card__img{
		margin: 0 15px 0 0;
	}
	.dr-card__info{
		width: calc(100% - 150px);
	}
	.dr-card__title{
		text-align: left;
	}
	.dr-card__list{
		width: 47%;
		padding: 0 15px 0 0;
		box-sizing: border-box;
	}
	.dr-card__item{
		text-align: left;
	}

}
@media (min-width: 1024px){
	//team
	.dr-card__img{
		margin: 0 17px 0 0;
	}
	.dr-card__info{
		width: calc(100% - 150px);
	}
	.dr-card__title{
		font-size: 20px;
	}
	.dr-card__list{
		width: 48%;
	}
	.dr-card__item{
		text-align: left;
		font-size: 16px;
	}
	//team-group
	.team-group__card{
		max-width: 432px;
		padding: 50px 30px;
		box-sizing: border-box;
	}
	.team-group {
		.team-group-leftBg {
			width: 50%;
			height: 100%;
		}
		&.team-group:after {
			top: 0;
			width: 50%;
			height: 100%;
		}
		.img {
			top: -55px;
			right: 0px;
			width: 195px;
			z-index: 0;
		}
	}
	.team-group__img{
		margin: 0 20px 0 0;
	}
	.team-group__info{
		text-align: left;
		padding: 12px 0 0 0;
		width: calc(100% - 200px);
	}
}
@media (min-width: 1280px){
	//team
	.team{
		padding: 90px 0;
	}
	.dr-card__img {
		margin: 0 50px 0 0;
	}
	.dr-card__info {
		width: calc(100% - 190px);
	}
	//team-group
	.team-group__card{
		//max-width: 540px;
		box-sizing: border-box;
	}
	.rightBg{
		.team-group__card{
			padding: 90px 0 90px 60px;
		}
	}
	.leftBg{
		.team-group__card{
			padding: 90px 60px 90px 0;
		}
	}
}
@media (min-width: 1440px){
	//team
	.team{
		padding: 125px 0;
	}
	//team-group
	.team-group__card{
		//max-width: 580px;
	}
	.rightBg{
		.team-group__card{
			padding: 129px 0 130px 125px;
		}
	}
	.leftBg{
		.team-group__card{
			padding: 129px 100px 130px 5px;
		}
	}
}
@media (min-width: 1600px){
	//team
	.dr-card__img {
		margin: 0 90px 0 0;
	}
	.dr-card__info {
		width: calc(100% - 230px);
	}
	//team-group
}
@media (min-width: 1920px){
	//team
	.uberUns{
		.footer {
			padding: 214px 0 25px 0;
		}
	}
	.team{
		padding: 264px 0 226px 0;
		.title-section{
			margin: 0 0 11px 0;
		}
		.title-text {
			margin: 0 0 90px 0px;
		}
	}
	.team__dr-card{
		padding: 0px 0 0 0px;
	}
	.dr-card__img {
		width: 163px;
		height: 206px;
		margin: 7px 78px 0 0;
	}
	.dr-card__info {
		width: calc(100% - 270px);
		align-self: flex-start;
	}
	.dr-card__title {
		margin: 0 0 32px 0;
		font-size: 28px;
		line-height: 1.6;
	}
	.dr-card__item {
		font-size: 18px;
		margin: 0 0 16px 0;
	}
	//team-group
	.team-group {
		.container{
			padding: 0 163px 0 274px;
		}
	}
	.team-group__img {
		width: 225px;
		height: 296px;
		margin: 0 50px 0 0;
	}
	.team-group__info{
		padding: 11px 0 0 0;
		width: calc(100% - 312px);
	}
	.team-group__name {
		font-size: 28px;
		margin: 0 0 44px 0;
	}
	.team-group__item {
		font-size: 16px;
		margin: 0 0 13px 0;
	}
	.team-group {
		.img {
			top: -87px;
			right: -6px;
			width: 411px;
		}
	}
	.team-group__card{
		padding: 0;
		max-width: 673px;
	}
	.team-group__img.logoIcon:before {
		width: 269px;
		height: 350px;
		background-size: contain;
		position: absolute;
		left: -212px;
		top: -8px;
		background-repeat: no-repeat;
	}
}
