@import 'element';
body {
	position: relative;
	height: 100%;
	font-family: $fontLora;
	font-size: 18px;
	line-height: 1.5;
	color: $color3;
	font-style: normal;
	letter-spacing: 0;
	font-weight: 400;
}
html {
	box-sizing: border-box;
	position: relative;
	height: 100%;
}
* {
	margin: 0;
	padding: 0;
	list-style: none;
	outline: none;
	text-decoration: none;
}
*:before,
*:after {
	box-sizing: inherit;
}
.wrapper {
	position: relative;
	margin: 0 auto;
	overflow: hidden;
}
img {
	max-width: 100%;
	height: auto;
}
svg {
	max-width: 100%;
	object-fit: cover;
	height: auto;
}
input[type="submit"] {
	appearance: none;
}
button {
	cursor: pointer;
	border: none;
}
a {
	cursor: pointer;
	transition: all 0.5s;
	&:hover{
		color:$color2;
	}
}
.container {
	padding: 0 15px;
	margin: 0 auto;
	position: relative;
	box-sizing: border-box;
	width: 100%;
}
.flex {
	display: flex;
	justify-content: space-between;
	flex-wrap: wrap;
	width: 100%;
}
.btn {
	padding: 12px 25px 12px 25px;
	background-color: $color4;
	cursor: pointer;
	font-size: 14px;
	font-style: normal;
	line-height: 1.1;
	text-align: center;
	color: $color1;
	text-transform: uppercase;
	transition: 0.5s;
	font-family: $fontArchivo;
	font-weight: 600;
	display: inline-block;
	&:hover{
		background:$color2;
		color: $color4;
	}
}
.section {
	position: relative;
}
::-webkit-scrollbar {
	width: 5px;
	height: 8px;
	background-color: $color1;
}
::-webkit-scrollbar-thumb {
	background-color: $color4;
	border-radius: 2px;
}
.title{
	font-family: $fontLora;
	color: $color2;
	margin: 0;
	padding: 0;
	font-size: 35px;
	font-weight: 400;
	line-height: 1.09;
}
.title-section{
	color:$color2;
	font-family: $fontArchivo;
	font-weight: 700;
	line-height: 1.2;
	font-size: 30px;
	margin: 0 0 10px 0px;
}
.title-text{
	font-family: $fontArchivo;
	font-weight: 400;
	color: $color4;
	font-size: 16px;
	margin: 0 0 20px 0;
	//line-height: 1.5;
	line-height: 1.9;
}
.scroll{
	color: $color4;
	font-size: 12px;
	letter-spacing: 0.3px;
	font-family: $fontArchivo;
	font-weight: 400;
	position: absolute;
	bottom: 15px;
	left: 0;
	writing-mode: vertical-rl;
	transition: 0.5s;
	display: inline-block;
	z-index: 2;
	&:hover{
		color: $color3;
	}
}
@media (min-width: 768px) {
	.container {
		width: 648px;
		box-sizing: border-box;
	}
	.container-right{
		padding: 0 0 0 100px;
	}
}
@media (min-width: 1024px) {
	.container {
		width: 864px;
	}
	.title{
		font-size: 60px;
	}
	.title-section{
		font-size: 35px;
	}
	.title-text{
		font-size: 18px;
	}
	.container-right{
		padding: 0 0 0 144px;
	}
}
@media (min-width: 1280px) {
	.container {
		width: 1080px;
	}
	.btn {
		padding: 18px 43px 18px 44px;
		font-size: 16px;
	}
	.title{
		font-size: 90px;
	}
	.title-section{
		font-size: 60px;
	}
	.container-right{
		padding: 0 0 0 187px;
	}
}
@media (min-width: 1440px) {
	.container {
		width: 1160px;
	}
	.container-right{
		padding: 0px 0 0 202px;
	}
}
@media (min-width: 1600px) {
	.container {
		width: 1280px;
	}
	.container-right{
		padding: 0px 0 0 226px;
	}
}
@media (min-width: 1920px) {
	.container {
		width: 1810px;
		padding: 0 17px 0 112px;
	}
	.title{
		font-size: 111px;
	}
	.title-section{
		font-size: 72px;
	}
	.scroll{
		bottom: 30px;
		left: 107px;
		font-size: 14px;
	}
	.container-right{
		padding: 0 0 0 242px;
	}
}
