@import 'element';
.header{
	padding: 25px 0 25px 0;
	z-index: 5;
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	background: $color1;
	transition: 0.5s;
	&.fixed{
		box-shadow: 0 0 10px rgba(93,107,69,0.5);
		padding: 10px 0;
		.menu__link{
			&.active{
				&:before{
					transition: 0.5s;
				}
			}
		}
	}
}
.header__logo{
	transition: 0.5s;
	width: 130px;
	height: auto;
	display: block;
	margin: 0 auto 20px;
}
.menu{
	display: flex;
	justify-content: center;
	padding:0;
	margin:0 auto ;
	align-self: center;
	width: 100%;
	transition: 0.5s;
	flex-wrap: wrap;
}
.menu__item{
	margin: 0 10% 0 0;
	&:last-child{
		margin: 0;
	}
}
.menu__link{
	font-family: 'Archivo';
	font-size: 15px;
	letter-spacing: 0;
	color: $color5;
	transition: 0.5s;
	position: relative;
	&.active{
		color: $color4;
		&:before{
			content: '';
			width: 100%;
			height: 5px;
			background-color: $color4;
			position: absolute;
			top: -8px;
			left: 0;
			transition: 0.5s;
			cursor: default;
			pointer-events: none;
		}
	}
	&:hover{
		color: $color2;
	}
}
@media (min-width: 768px){
	.header{
		&.fixed{
			.menu__link{
				&.active{
					&:before {
						top: -32px;
						height: 10px;
					}
				}
			}
		}
	}
	.header__logo{
		margin: 0;
		width: 166px;
		height: 58px;
	}
	.menu{
		width: calc(100% - 180px);
		margin: 0;
		justify-content: flex-end;
	}
	.menu__item{
		&:last-child{
			margin: 0;
		}
	}
	.menu__link{
		&.active{
			&:before{
				height: 15px;
				top: -48px;
			}
		}
	}
}

@media (min-width: 1280px){
	.header{
		padding: 30px 0;
	}
	.menu__link {
		&.active {
			&:before {
				top: -52px;
			}
		}
	}
}
@media (min-width: 1920px){
	.header{
		padding: 57px 0 0 0;
	}
	.menu__item{
		margin: 0 122px 0 0;
		&:last-child{
			margin: 0;
		}
	}
	.menu__link{
		font-size: 15px;
		letter-spacing: 0;
	}
	.menu__link{
		&.active{
			&:before{
				top: -80px;
			}
		}
	}
}
